* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;

  &__input-title {
    width: 100%;
    padding: 12px 20px;
    margin-bottom: 8px;
    box-sizing: border-box;

    &:focus {
      border: 3px solid #555;
    }
  }

  &__form-title {
    text-align: center;
  }

  &__select {
    width: 100%;
    padding: 16px 20px;
    border: none;
    border-radius: 4px;
    background-color: #f1f1f1;
    margin-bottom: 8px;
  }

  &__label {
    margin-right: 15px;
    line-height: 32px;
  }

  &__radio {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    border-radius: 50%;
    width: 16px;
    height: 16px;

    border: 2px solid #999;
    transition: 0.2s all linear;
    margin-right: 5px;

    position: relative;
    top: 4px;

    &:checked {
      border: 4px solid black;
    }
  }

  &__button {
    background-color: #555;
    border-radius: 5px;
    color: #f1f1f1;
    padding: 10px;
  }

  &__list {
    list-style: none;
  }

  &__item {
    border: 1px solid green;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}
